<template>
	<div class="container">
		<p>
			<Icon type="ios-checkmark-circle-outline"></Icon>
		</p>
		<router-link to="/login">
			<Button type="success" size="large" long class="btn-success">{{strings}}</Button>
		</router-link>
	</div>
</template>

<script>
	import {mapMutations} from 'vuex';
	export default {
    name: 'SignUpDone',
    data(){
      return {
        strings:"注册成功"
      }
    },
		mounted() {
      if(sessionStorage.getItem("regStatus")==1){
        this.strings="找回成功"
      }
			this.SET_SIGN_UP_STEP(2);
    },
		methods: {
			...mapMutations(['SET_SIGN_UP_STEP']),
		},
	};
</script>

<style scoped>
	.container {
		width: 100%;
		text-align: center;
	}

	.container p {
		font-size: 90px;
		color: #2d8cf0;
		text-align: center;
	}

	.btn-success {
		margin-top: 30px;
	}
</style>



